/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

const mouse = new THREE.Vector2()
window.addEventListener('mousemove', (event) =>
{
    mouse.x = (event.clientX / sizes.width) * 2 - 1
    mouse.y =  - (event.clientY /sizes.height) * 2 + 1
})

let modelActive = false
let modelLock = false
const closeBtn = document.querySelector(".close-button")

// Set properties to configure:
let airflow1 = new Text();
airflow1.text = '<'
airflow1.fontSize = 0.5
airflow1.position.x =  1
airflow1.position.y =  1
airflow1.position.z =  0.2
airflow1.color = 0x000000
airflow1.material = new THREE.MeshBasicMaterial({side: THREE.DoubleSide})

let airflow2 = new Text()
airflow2.text = '<'
airflow2.fontSize = 0.5
airflow2.position.x =  -2
airflow2.position.y =  0.2
airflow2.position.z =  3.3
airflow2.color = 0x000000
airflow2.material = new THREE.MeshBasicMaterial({side: THREE.DoubleSide})


window.addEventListener('click', ()=>
{
    
    const rotationY = currentModelY> Math.PI ? currentModelY + (Math.PI * 2-currentModelY): 0
    if(modelActive && !modelLock)
    {
        // webCam()
        let timeline = gsap.timeline()
        timeline.to(camera, {
            duration: 2,
            zoom: 1,
            onUpdate: function () {
                camera.updateProjectionMatrix()
                camera.lookAt(0,1,0)
            }
        },0)
        timeline.to(camera.position,
            {
                duration:1,
                x: 4,
                y: 3,
                z: 5,
                onUpdate: function () {
                    camera.updateProjectionMatrix();
                    scene.add(airflow1)
                    scene.add(airflow2)
                }
            },0)
        timeline.to(model.rotation,
                {
                    duration: 1,
                    y: rotationY,
                    ease: "none",
                },0)
        timeline.to(model.position,
            {
                duration: 2,
                x: 0,
                z: 0,
                ease: "elastic.out",
                delay: 0.2
            },"<1")

        modelRotate = false
        selected = true
        modelActive = false
        window.setTimeout(()=>
        {
            closeBtn.classList.add("enabled")
            selected = true
            modelActive = false
            controls.enableRotate = true
            controls.enableZoom = true
        }, 2000)
    }
})

const clickedClose = document.getElementById("close-view")
clickedClose.onclick = () =>
{

    // stopCam()
    // svideo.currentTime = 0;
    scene.remove(airflow1)
    scene.remove(airflow2)
    airflow1.dispose()
    airflow2.dispose()
    gsap.to(camera, {
        duration: 2,
        zoom: 0.5,
        onUpdate: function () {
            camera.updateProjectionMatrix();
            // svideo.pause();
        }
    })
    gsap.to(camera.position,
        {
            duration:5,
            x: 2,
            y: 2,
            z: 6,
            onUpdate: function () {
                camera.updateProjectionMatrix();
            }
        })

    closeBtn.classList.remove("enabled")
    selected = false
    modelRotate = false
    modelLock = false
    controls.enableRotate = false
    controls.enableZoom = false
    return false
}

/**
 * Camera
 */
// Base camera
const cameraParams = {
    x: 2,
    y: 2,
    z: 6,
    zoom: 0.5
}
const camera = new THREE.PerspectiveCamera(45, sizes.width / sizes.height, 1, 1000)
camera.position.x = cameraParams.x //4
camera.position.y = cameraParams.y
camera.position.z = cameraParams.z
camera.lookAt(0,1,0)
camera.zoom = cameraParams.zoom // 0.5
camera.updateProjectionMatrix();
scene.add(camera)

const cameraFolder = pane.addFolder({
    title: "Camera",
    expanded: false
})
cameraFolder.addInput(cameraParams, 'x', {min:-10, max:20}).on('change', ()=>{camera.position.x = cameraParams.x})
cameraFolder.addInput(cameraParams, 'y', {min:-10, max:20}).on('change', ()=>{camera.position.y = cameraParams.y})
cameraFolder.addInput(cameraParams, 'z', {min:-10, max:20}).on('change', ()=>{camera.position.z = cameraParams.z})
cameraFolder.addInput(cameraParams, 'zoom', {min:0, max:5, step:0.1}).on('change', ()=>{
    camera.zoom = cameraParams.zoom
    camera.updateProjectionMatrix()
})


// Controls
