controlsFolder.addInput(controlsParams, 'enablePan').on('change', ()=>{controls.update()})
controlsFolder.addInput(controlsParams, 'enableRotate').on('change', ()=>{controls.update()})
controlsFolder.addInput(controlsParams, 'enableZoom').on('change', ()=>{controls.update()})
controlsFolder.addInput(controlsParams, 'autoRotate').on('change', ()=>{controls.update()})
