function mouseUp()
{
    window.removeEventListener('mousemove', divMove, true);
}

function mouseDown(e){
    window.addEventListener('mousemove', divMove, true)
}

function divMove(e){
    // var div = document.getElementById('debugelement');
  debugContainer.style.position = 'absolute';
  debugContainer.style.top = e.clientY + 'px';
  debugContainer.style.left = e.clientX + 'px';
}

document.querySelector('.tp-rotv_b').addEventListener('mousedown', mouseDown, false);
window.addEventListener('mouseup', mouseUp, false);

function drawGraph() {
    const dataCount = dataArray.length()
    var dataLabels = []
    var data = {
        labels: dataLabels,
        datasets: []
    }
}


/**
 * Animate
 */
const clock = new THREE.Clock()
let n = 0
let arrayN = 0

let selected = false
let tl = gsap.timeline({repeatDelay: 2, paused:true});
let j = 0
let pass = 0

let previousTime = 0

let currentModelY

function parseHexString(str) { 
    var result = [];
    while (str.length >= 8) { 
        result.push(parseInt(str.substring(0, 8), 16));

        str = str.substring(8, str.length);
    }

    return result;
}

const tick = () =>
{
    var today = new Date()
    title.innerHTML = `LIVE from ATG, Ipoh @ ${today.getFullYear()+'-'+(today.getMonth()+1)+'-'+today.getDate()}
     ${today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds()}`
    // console.log(today.getFullYear()+'-'+(today.getMonth()+1)+'-'+today.getDate());
    
    pane.refresh()
    if(dataArray)
    {
        // console.log( new Number(dataArray[dataArray.length - 1]["ar_reading_5"]).toFixed(50)*1000000000000000000000000000000000000000000);
        // console.log( Number(dataArray[dataArray.length - 1]["ar_reading_5"]).toLocaleString());
        // console.log(Number(dataArray[dataArray.length - 1]["ar_reading_5"]));
        displayData.innerHTML = `Gas Temp : ${dataArray[dataArray.length - 1]["ar_reading_1"]}<br>
            Gas Pressure: ${dataArray[dataArray.length - 1]["ar_reading_2"]}<br>
            Flow Meter Temp: ${dataArray[dataArray.length - 1]["ar_reading_3"]}<br>
            Flow Meter Pressure: ${dataArray[dataArray.length - 1]["ar_reading_4"]}<br>`
        document.querySelector('.point-4 .text').innerHTML = `Total Flow Rate (Gas) : ${dataArray[dataArray.length - 1]["ar_reading_6"]}`
    }
    const elapsedTime = clock.getElapsedTime()
    const deltaTime = elapsedTime - previousTime
    previousTime = elapsedTime
    if(Math.round(elapsedTime) % 1 === 0 && Math.round(elapsedTime)!= n)
    {
        const initBackground = '#00000077'
        const initColor = '#ffffff'
        const ranInt = Math.floor(Math.random() * 4)
        const labelLength = airflow1.text.length
        if (Math.round(elapsedTime) != pass){
            
            for (const point of points)
            {
                if(ranInt === point.id)
                {
                    point.labelElement.style.background = 'green'
                    point.labelElement.style.color = 'black'
                } else {
                    point.labelElement.style.background = initBackground
                    point.labelElement.style.color = initColor
                }
            }
            if(labelLength === 1){
                airflow1.text = '<<'
                airflow2.text = '<<'
                pass = Math.round(elapsedTime)
            }
            else if(labelLength === 2){
                airflow1.text = '<<<'
                airflow2.text = '<<<'
                pass = Math.round(elapsedTime)
            }
            else if(labelLength === 3){
                airflow1.text = '<'
                airflow2.text = '<'
                pass = Math.round(elapsedTime)
            }
        }
    }
    if(Math.round(elapsedTime) % 30 === 0 && Math.round(elapsedTime)!= n)
    {   
        // console.log('updating')
        fetch('https://go-atg.com.my/api-iot/index.php?action=showLatest30ArduinoReading')
            .then(response => response.json())
            .then(data => {
                // console.log(data['results'], data['results'][data['results'].length -1])
                dataArray = data['results']
                graph.update(
                    myChart, 
                    data['results'][data['results'].length -1],
                    new Date().getTime()
                    )
            })
        n = Math.round(elapsedTime)
        arrayN ++
        displayData.innerHTML = `Gas Temp : ${dataArray[dataArray.length - 1]["ar_reading_1"]}<br>
        Gas Pressure: ${dataArray[dataArray.length - 1]["ar_reading_2"]}<br>
        Flow Meter Temp: ${dataArray[dataArray.length - 1]["ar_reading_3"]}<br>
        Flow Meter Pressure: ${dataArray[dataArray.length - 1]["ar_reading_4"]}<br>`
        for(const point of points)
        {
            switch(point.id)
            {
                // case 0:
                //     point.textElement.innerHTML = `TEMP After Vapour: ${parseInt(dataArray[0]).toFixed(2)}<br>
                //     Pressure after vaporization : ${parseInt(dataArray[1]).toFixed(2)}`
                //     break;
                // case 1:
                //     point.textElement.innerHTML = `Temperature of Flow : ${parseInt(dataArray[2]).toFixed(2)}<br>
                //     Pressure in in flow meter : ${parseInt(dataArray[3]).toFixed(2)}`
                //     break
                case 4:
                    point.textElement.innerHTML = `Total Flow Rate (Gas) : ${dataArray[dataArray.length - 1]["ar_reading_5"]}`
                    break
            }
        }
    }
    if(loaded)
    {
        //test model animation
        gsap.to(model.position,
            {
                duration: 1,
                y: -0.5,
                ease: "elastic.inout",
                delay: 1.5
            })
        raycaster.setFromCamera(mouse, camera)
        const mouseIntersect = raycaster.intersectObject(model, true)
        if(mouseIntersect.length)
        {
            modelActive = true
            if (rotate){
                controls.autoRotate = true
            }
        }
        else
        {
            modelActive = false
        }

        if(Math.round(model.position.y, 0) === 0)
        {
            if(modelRotate)
            {
                model.rotation.y += 0.002
                model.rotation.y %= Math.PI * 2
                currentModelY = model.rotation.y
            }
            if(selected)
            {
                featureContainer.classList.add('visible')
                instruction.style.opacity = 0
                modelLock = true
                container.classList.add('visible')
                title.classList.add('visible')
                for(const point of points)
                {
                    const screenPosition = point.position.clone() //dont modify original position
                    screenPosition.project(camera)

                    raycaster.setFromCamera(screenPosition, camera)
                    const intersects = raycaster.intersectObjects(scene.children, true)
                    if(intersects.length === 0)
                    {
                        if(model.position.y === 0)
                        {
                            point.element.classList.add('visible')
                        }
                    }
                    else
                    {
                        const intersectionDistance = intersects[0].distance
                        const pointDistance = point.position.distanceTo(camera.position)
                        if(intersectionDistance < pointDistance)
                        {
                            point.element.classList.remove('visible')

                        }
                        else
                        {
                            point.element.classList.add('visible')
                        }
                    }
                    const translateX = screenPosition.x * sizes.width * 0.5
                    const translateY = -(screenPosition.y * sizes.height * 0.5)
                    point.element.style.transform = `translate(${translateX}px, ${translateY}px)`
                }
            }
            else
            {
                title.classList.remove('visible')
                featureContainer.classList.remove('visible')
                for(const point of points)
                {
                    point.element.classList.remove('visible')
                    container.classList.remove('visible')
                    instruction.style.opacity = 1
                }
            }
        }
    }

    // Update controls
    controls.update()

    airflow1.sync()
    airflow2.sync()
    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()
