import Chart from 'chart.js/auto';
import colorLib from '@kurkle/color';

export class Graph {
    constructor(data, time){
        data = data || {}
        this.data = data
        this.time = time
        this.labels = []

        this.data1 = this.template('Gas Temp', 'line 1', 'rgb(255,0,0)',1)
        this.data2 = this.template('Gas Pressure', 'line 2', 'rgb(255,0,0)',1)
        this.data3 = this.template('Flow Meter Temp', 'line 3', 'rgb(255,0,0)',1)
        this.data4 = this.template('Flow Meter Pressure', 'line 4', 'rgb(255,0,0)',1)
        this.dataSet = [this.data1, this.data2, this.data3, this.data4]
        this.parseData((data.length > 0) ? true: false)
    }

    parseData(callback){
        if(callback){
            this.data.forEach(sets => {
                this.data1.data.push(parseFloat(sets['ar_reading_1']))
                this.data2.data.push(parseFloat(sets['ar_reading_2']))
                this.data3.data.push(parseFloat(sets['ar_reading_3']))
                this.data4.data.push(parseFloat(sets['ar_reading_4']))
            })
            if (this.labels.length <= 0){
                for (let i = 0; i < 30; i++)
                {
                    this.labels.push(new Date((this.time - 900*1000) + i*30*1000).toLocaleString())
                }
            }
        }
    }

    update(chart, data, time){
        this.labels.shift()
        this.labels.push(new Date((time)).toLocaleString())
        chart.update()
        this.data1.data.shift()
        this.data1.data.push(parseFloat(data['ar_reading_1']))
        chart.update()
        this.data2.data.shift()
        this.data2.data.push(parseFloat(data['ar_reading_2']))
        chart.update()
        this.data3.data.shift()
        this.data3.data.push(parseFloat(data['ar_reading_3']))
        chart.update()
        this.data4.data.shift()
        this.data4.data.push(parseFloat(data['ar_reading_4']))
        chart.update()
        
    }

    singleLine(lineNum)
    {
        var config = {
            type: 'line',
            data: {
                labels: this.labels,
                datasets: []
            },
            options: {
                responsive: true,
                scales: {
                    y: {
                        beginAtZero: true
                    }
                }
            }
        }
        switch(lineNum){
            case 1:
                config.data.datasets.push(this.data1)
                break
            case 2:
                config.data.datasets.push(this.data2)
                break
            case 3:
                config.data.datasets.push(this.data3)
                break
            case 4:
                config.data.datasets.push(this.data4)
                break
        }
        return config
    }

    draw(config, element){
        return new Chart(element, config)
    }

    template(label, yaxis, rgb, opacity){
        return {
            label: label,
            data: [],
            borderColor: rgb,
            backgroundColor: colorLib(rgb).alpha(opacity).rgbString(),
            yAxisID: yaxis
        }
    }
    
}